ding: 12px;\r\n    border: 1px solid #d9d9d9;\r\n    border-radius: 5px;\r\n    background: transparent;\r\n    font-family: inherit;\r\n    font-size: 16px;\r\n    font-weight: 600;\r\n    transition: all .2s;\r\n    cursor: pointer;\r\n\r\n    &:hover {\r\n      background-color: $controlsColor;\r\n      border-color: $controlsColor;\r\n      color: #fff;\r\n\r\n      &::after, &::before {\r\n        color: #e8e2f1;\r\n      }\r\n    }\r\n  }\r\n\r\n  &__current-date-btn {\r\n    font-size: inherit;\r\n    font-weight: inherit;\r\n    line-height: 120%;\r\n    padding: 13px 20px 12px;\r\n    border: 1px solid #e8e2f1;\r\n    cursor: pointer;\r\n    transition: background-color .2s;\r\n\r\n    &:hover {\r\n      background-color: #d9d9d9;\r\n    }\r\n\r\n    &[data-year] {\r\n      border-color: $selectedFirst;\r\n    }\r\n  }\r\n\r\n  &__top {\r\n    display: grid;\r\n    grid-template-columns: repeat(7, 1fr);\r\n  }\r\n\r\n  &__top-description {\r\n    text-align: center;\r\n    font-size: 17px;\r\n    padding: 6px;\r\n  }\r\n\r\n  &__display {\r\n    display: grid;\r\n    transition: all .2s;\r\n\r\n    &[data-display=\"days\"] {\r\n      grid-template-columns: repeat(7, 1fr);\r\n      gap: 4px;\r\n    }\r\n\r\n    &[data-display=\"months\"] {\r\n      grid-template-columns: repeat(4, 1fr);\r\n      gap: 12px;\r\n      margin-top: 12px;\r\n    }\r\n  }\r\n\r\n  &__cell {\r\n    text-align: center;\r\n    padding: 12px;\r\n    border-radius: 3px;\r\n    border: 1px solid #d9d9d9;\r\n    cursor: pointer;\r\n    font-weight: 600;\r\n    transition: all .2s;\r\n\r\n    &:hover {\r\n      background-color: $accent;\r\n      color: #fff;\r\n    }\r\n\r\n    &[data-month] {\r\n      padding: 24px 12px;\r\n      font-weight: 400;\r\n      border-radius: 8px;\r\n      &:hover {\r\n        background-color: $selectedFirst;\r\n      }\r\n    }\r\n\r\n    &--now {\r\n      background-color: $current;\r\n      color: #fff;\r\n    }\r\n\r\n    &[data-selected=\"first\"] {\r\n